<template>
  <div class="hello">
    <!-- headder-start -->
			<banner></banner>
    <!-- headder-end -->
		
		
		<!-- 分类-start -->
			<classify></classify>
		<!-- 分类-end -->
		
		
		<!-- 圈子-start -->
			<message></message>
		<!-- 圈子-end -->
		
		<!-- 拼团-start -->
			<spellGroup></spellGroup>
		<!-- 拼团-end -->
		
		<!-- 活动-start -->
			<activity></activity>
		<!-- 活动-end -->
		
		<!-- 猜你喜欢-start -->
			<like></like>
		<!-- 猜你喜欢-end -->
		
		<!-- 红包-start -->
			<coupons></coupons>
		<!-- 红包-end -->
		
		<!-- 商品-start -->
			<goods v-for='(item,index) in goodsData' :data='item' :key='index'></goods>
		<!-- 商品-end -->
		
		<!-- 分页-start -->
			<classifyGoods></classifyGoods>
		<!-- 分页-end -->
  </div>
</template>

<script>
import banner from './banner'
import classify from './classify'
import message from './message'
import spellGroup from './spellGroup'
import activity from './activity'
import like from './like'
import coupons from './coupons'
import goods from './goods'
import classifyGoods from './classifyGoods'
export default {
  name: 'HelloWorld',
  data () {
    return {
      goodsData:[
				{imgUrl:'',goods:[{imgUrl:'',text:'男士短袖t恤',price:198}]},
			]
    }
  },
	components:{
		banner,
		classify,
		message,
		spellGroup,
		activity,
		like,
		coupons,
		goods,
		classifyGoods,
	},
	methods:{
		getGoodsList:function(){
			this.axios({
				url:'goods',
				method:'get',
			}).then((res)=>{
				if(res.data.status == 200){
					this.goodsData = res.data.data;
				}else{
					alert("数据获取失败")
				}
			})
		}
	},
	created(){
		this.getGoodsList();
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
*{
	margin:0;
	padding:0;
}
.hello{
	margin-bottom:15vw;
}
</style>



